@import url("https://fonts.googleapis.com/css2?family=Cormorant+Upright:wght@300;400;500;600&family=Open+Sans:wght@300;400;500;600&display=swap");
:root {
  --font-base: "EB Garamond", serif;
  --font-alt: "Open Sans", sans-serif;
  --color-blue: #060ef2;
  --color-black: #0c0c0c;
  --color-gray: #545454;
  --color-crimson: #f5efdb;
  --color-grey: #aaaaaa;
  --color-white: #ffffff;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}
a {
  color: unset;
  text-decoration: none;
}
